@charset "UTF-8";

.kingdom_bg {
    background-image: url(../../../public/images/buildings/spring/bg.webp);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    transition: background-image .3s ease-out;
}

.move_bx {
    background-color: rgba(255, 255, 255, 0.7);
}

.buildings_tgbx {
    height: calc(100vh + 6000px);
}

.buildings {
    position: sticky;
    top: 0;
    left: 0;
    height: 100vh;
    overflow: hidden;
}

.buildings ul {
    position: relative;
    display: flex;
    width : calc(100vw + 5000px);
    height: 100%;
    left: 0;
}

.building {
    position: relative;
    flex : 1;
    height: 100%;
}

.building_bx {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    transition: .4s ease-in-out;
}

.building_bx.on {
    bottom: 12%;
    transform: translateX(-50%) scale(1.2);
}

.building_img {
    aspect-ratio: 1/1;
    height: 45vh;
}

.building_img figure {
    position: absolute;
}

.building_build img{
    height: 100%;
    width: 100%;
}

.building_cookie {
    bottom: 0;
    left: calc(50% - 150px);
    width : 32%;
    transform: translateY(20%);
    opacity: 0;
    transition: transform 0.8s cubic-bezier(0.075, 0.82, 0.165, 1) .3s,opacity 0.8s cubic-bezier(0.075, 0.82, 0.165, 1) .3s;
}

.building:first-child .building_cookie {
    width: 40%;
}

.building_cookie.on {
    opacity: 1;
    transform: translateY(0);
}

.building_cookie img {
    width: 100%;
}

.building_btn {
    opacity: 0;
    position: absolute;
    bottom: -10%;
    left: 50%;
    transform: translate(-50%, 20%);
    transition: transform 0.8s cubic-bezier(0.075, 0.82, 0.165, 1) .3s,opacity 0.8s cubic-bezier(0.075, 0.82, 0.165, 1) .3s;
}

.building_btn.on {
    opacity: 1;
    transform: translate(-50%, 0);
}

