.talk_no, .close_talk, .pretalk_btn .close_talk {
    background: -1.5px -2px/cover no-repeat radial-gradient(closest-side at 15% 30%, #fff 25%, transparent 30%), radial-gradient(farthest-corner at 250% 450%, #f03e3e 88%, #fa5252 88%);    
    border-top: 2px solid #ffa8a8;
    border-left: 2px solid #ffa8a8;
	border-bottom: 2px solid #c92a2a;
	border-right: 2px solid #c92a2a;
}

.building_btn button:hover, .talk_nextbtn button:hover, .pretalk_btn button:hover {
    filter: brightness(.9);
}

.cookie {
    position: fixed;
    bottom: 5%;
    left: 20%;
    height: 15%;
    z-index: 999;
}

.cookie2 {
    position: fixed;
    left: 40%;
    top: 50%;
    height: 30%;
    transform: translate(-50%, -50%);
    z-index: 999;
