.cookie_msg {
    font-size: min(20px, 1.4vw);
    white-space: nowrap;
    text-align: center;
}

.cookie_msg .mouse-wrap {
    top: auto;
    bottom: 0;
}

.cookie_talk {
    display: none;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 9999;
}

.cookie_talkbx {
    position: absolute;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    top: 50%;
    height: 80%;
    left: 0;
    width: 100%;
    transform: translateY(-50%);
    transition: background-image .3s ease-out;
}

.cookie_talkwrap {
    position: absolute;
    width: 80%;
    height: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.talk_img {
    position: absolute;
    bottom: 10%;
    width: 100%;
}

.talk_img figure {
    position: absolute;
    bottom: 0;
    width: 35%;
}

.talk_img2 {
    right: 0;
    transform: rotateY(180deg);
    display : none;
}

.talk_img img{
    width: 100%;
}

.talk_img1 img {
    display: block;
    position: absolute;
    bottom: 0;
}

.talk_img1 img:nth-child(2), .talk_img1 img:nth-child(3){
    display: none;
}

.talk_bx {
    position: absolute;
    width: 85%;
    height: 15%;
    font-size: min(16px, 1.8vw);
    bottom: 2%;
    left: 50%;
    transform: translateX(-50%);
    background-color: #fef3ef;
    border-radius: 100px;
    border: 1px solid #555;
    color : #555;
}


.talk_name {
    position: absolute;
    width: auto;
    height: auto;
    top: -10%;
    left: 5%;
    padding : 5px 20px;
    background-color: #bbb;
    border-radius: 30px;
    border : 3px solid #fff;
    color : #fff;
}

.talk_name2 {
    left: auto;
    right: 5%;
    display: none;
}

.talk_wrap {
    position: absolute;
    top: 20%;
    left: 5%;
}

.talk_nextbtn {
    position: absolute;
    left : 5%;
    bottom: 5%;
    transform: translateY(-50%);
}

.close_talk {
    display: none;
}

.pretalk_btn {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: none;
    z-index: 2;
}

.pretalk_btn.on {
    display: block;
}

.pretalk_btn>div {
    position: absolute;
    width: 50%;
    height: 20%;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background-color: rgba(0,0,0,0.5);
}

.pretalk_btn p {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    color : #fff;
    font-size: min(16px, 1.6vw);
    text-shadow:
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        0 0 .1em #000,
        /* Long Shadow */
        0 .01em .1em #000,
        0 .1em .1em #000,
        0 .1em .1em #000,
        0 .1em .1em #000,
        0 .1em .1em #000;
}

.pretalk_wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.pretalk_wrap .close_talk {
    display: inline-block;
}

@media screen and (max-width: 600px) {
    .talk_img figure {
        width: 60%;
    }

    .talk_nextbtn {
        bottom: -14%;
    }
}